import semver from 'semver';
import DocsSource from './DocsSource';

const branchBlacklist = new Set(['docs', 'parsers', 'typehandler', 'stable', '7.x']);
export default new DocsSource({
  id: 'main',
  name: 'Main library',
  global: 'Hypixel',
  repo: 'hypixel-api-reborn/hypixel-api-reborn',
  defaultTag: 'master',
  defaultClass: 'Client',
  branchFilter: branch => !branchBlacklist.has(branch),
  tagFilter: tag => semver.gte(tag, '3.0.0'),
});



